.album{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
  grid-gap: 32px;
}
/* .album-container{
    display: flex;
    justify-content: center;
    width: 100%;
} */
.album .album-card {
    position: relative;
    border-radius: 10px;
    background-clip: border-box;
    border: none;
  }
  
  .album .album-card .icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #f00;
    transition: 0.7s;
    z-index: 1;
  }
  
  .album .album-card:nth-child(1) .icon {
    background: #e07768;
  }
  
  .album .album-card:nth-child(2) .icon {
    background: #6eadd4;
  }
  
  .album .album-card:nth-child(3) .icon {
    background: #4aada9;
  }
  
  .album .album-card .icon .fa {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 80px;
    transition: 0.7s;
    color: #fff;
  }
  .container-right{
    grid-area: main;
    max-width: 90em;
    margin-left: auto;
    margin-right: auto;
  }
  
  i {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 80px;
    transition: 0.7s;
    color: #fff;
  }
  
  .album .album-card .face {
    width: 300px;
    height: 200px;
    transition: 0.5s;
  }
  
  .album .album-card .face.face1 {
    position: relative;
    background: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transform: translateY(100px);
  }
  
  .album .album-card:hover .face.face1 {
    background: #ff0057;
    transform: translateY(0px);
  }
  
  .album .album-card .face.face1 .content {
    opacity: 1;
    transition: 0.5s;
  }
  
  .album .album-card:hover .face.face1 .content {
    opacity: 1;
  }
  
  .album .album-card .face.face1 .content i {
    max-width: 100px;
  }
  
  .album .album-card .face.face2 {
    position: relative;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);
    transform: translateY(-100px);
  }
  
  .album .album-card:hover .face.face2 {
    transform: translateY(0);
  }
  
  .album .album-card .face.face2 .content p {
    margin: 0;
    padding: 0;
    text-align: center;
    color: #414141;
  }
  
  .album .album-card .face.face2 .content h3 {
    margin: 0 0 10px 0;
    padding: 0;
    color: #fff;
    font-size: 24px;
    text-align: center;
    color: #414141;
  }
  
  .album a {
    text-decoration: none;
    color: #414141;
  }
  
